@import "./variables";

.Chat-Item {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: row;
  flex-wrap: nowrap;
  cursor: pointer;
  width: 100%;
  height: 72px;

  img {
    grid-area: icon;
    object-position: center center;
    object-fit: cover;
    height: 50px;
    width: 50px;
    margin: 0 10px;
    border-radius: 50%;
  }

  .Chat-Info {
    width: 100%;

    .Chat-UserName {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      @include themify($themes) {
        color: themed('text-color');
      }

      .Chat-Date {
        font-size: 14px;
        margin-right: 10px;
        @include themify($themes) {
          color: themed('dark-text-color');
        }
      }
    }

    .Chat-LastMessage {
      @include themify($themes) {
        color: themed('dark-text-color');
      }
    }
  }

  a:link,
  a:visited,
  a:hover,
  a:active {
    text-decoration: none;
    @include themify($themes) {
      color: themed('text-color');
    }
  }

}

.Chat-Item:hover {
  @include themify($themes) {
    background: themed('background-color');
  }
}


